.project{
    display: flex;
    flex-direction: row;
    width: 100%;
    align-self: center;
    align-items: center;
    justify-content: center;
    padding: 0.5em;
    .projectActual{
        display: flex;
        flex-direction: row;
        width: 40%;
        .txt{
            display: flex;
            flex-direction: column;
            text-align: left;
            margin-right: 0.4em;
        }
        .image{
            display: flex;
            flex-direction: column;
            align-self: right;
            margin-left: auto;
            margin-right: 0em;
            align-items: center;
            justify-content: center;
            img{
                border-radius: 25px;
                max-height: 15em;
                align-self: last baseline;
                justify-self: right;
            }
        }
    }
}

@media (max-width: 3000px){
    .project{
        .projectActual{
            width: 70%;
        }
    }
}

@media(max-width: 1500px){
    .project{
        .projectActual{
            width: 95%;
        }
    }
}

@media(max-width: 1100px){
    .project{
        .projectActual{
            flex-direction:column;
            .txt{
                text-align: center;
            }
            .image{
                display: flex;
                flex-direction: column;
                align-self: center;
                margin-left: auto;
                margin-right: auto; 
            }
        }
    }
}

@media (max-width: 800px){
    .project{
        .projectActual{
            .image{
                img{
                    max-width: 80vw;
                }
            }
        }
    }
}